.list-group-item {
    padding: 15px 20px;
    border-bottom: 1px solid #3d5a80;
}

.list-group-item:last-child {
    border-bottom: none;
}

.list-group-item span {
    line-height: 35px;
    font-size: 20px;
    cursor: pointer;
    min-width: 550px;
}

.list-group-item input {
    line-height: 35px;
    font-size: 20px;
    text-align: center;
    border: 0;
}

.list-group-item button {
    width: 35px;
    height: 35px;
    margin: 3px;
    font-size: 17px;
    border: none;
    cursor: pointer;
}

.list-group-item .btn-cookie {
    color: #e09f3e;
}

.list-group-item .btn-trash {
    color: #e5383b;
}

.list-group-item .fa-star {
    width: 35px;
    height: 35px;
    text-align: center;
    line-height: 35px;
    font-size: 16px;
    color: #FFD700;
    transition: 0.3s all;
    transform: translateX(30px);
    opacity: 0;
}

.list-group-item.like .fa-star {
    opacity: 1;
    transform: translateX(0px);
}

.list-group-item.increase .list-group-item-label, .list-group-item.increase .list-group-item-input {
    color: #e09f3e;
}

.list-group-item-label {
    user-select: none;
}
  
.list-group-item.increase .btn-star {
    color: #aeaeae;
}
  
[data-tooltip] {
    position: relative
}

[data-tooltip]::after {
    content: attr(data-tooltip);
    position: absolute;
    font-size: 12px;
    right: 0;
    width: 200px;
    bottom: 0;
    background: #3d5a80;
    color: #fff;
    box-shadow: 2px 2px 5px rgb(0 0 0 / 30%);
    pointer-events: none;
    opacity: 0;
    transition: 1s;
    border-radius: 10px;
   } 
   [data-tooltip]:hover::after {
    opacity: 1; /* Показываем подсказку */
    bottom: 3em; /* Положение подсказки */
   }